<template>
  <div>
    <div class="tiemFixd">
      <timeList  @timeChang="timeChang"></timeList>
    </div>
<!--    <div class="reportOut">-->
<!--      <div class="report">-->
<!--        <div class="Earnings">-->
<!--          <p>累计收益</p>-->
<!--          <div>{{day}}</div>-->
<!--        </div>-->
<!--        <div class="price">-->
<!--          31,252.00-->
<!--        </div>-->
<!--        <div class="Number">-->
<!--          <div>-->
<!--            <p>累计门店数</p>-->
<!--            <p>10</p>-->
<!--          </div>-->
<!--          <div>-->
<!--            <p>累计订单数</p>-->
<!--            <p>10</p>-->
<!--          </div>-->
<!--        </div>-->
<!--      </div>-->
<!--    </div>-->
    <div class="shopList">
      <div class="headers">
        <p>子代理商名称</p>
        <p>累计收益</p>
        <p>订单数</p>
      </div>
      <ul v-if="shoplistShow">
        <li v-for="(item,index) in shopList"   @click="liClick(index)">
          <p :class="liActive==index?'':'textShow'"  >{{item.subAgentName}}</p>
          <p >{{item.orderAmount==null?0:item.orderAmount/100}}</p>
          <p>{{item.orderCount}}</p>
        </li>
      </ul>
      <ul v-else>
        <li><div>无数据</div></li>
      </ul>
    </div>

  </div>
</template>

<script>
  const timeList = () => import("../../../components/timeList.vue");
  export default {
    components:{
      timeList
    },
    data() {
      return {
        day:this.common.timeForMat(7,true)+" - "+this.common.timeForMat(0,true),
        active: 0,
        liActive:-1,
        shoplistShow:true,
        shopList:[],
        pathData:{
          beginDate:this.common.timeForMat(7,false),
          endDate:this.common.timeForMat(0,false),
        },
      }
    },
    mounted(){
      this.shopData();
    },
    methods:{
      shopData(){
        this.http.get3("csh_m/statCenter/subAgentRank/"+this.$store.state.pathCode,this.pathData,this.$store.state.token)
          .then(res => {
            console.log(res)

            if(res.data.code==600){
              this.shopList=res.data.result;
              this.shoplistShow=true;
              if(this.shopList.length<1){
                this.shoplistShow=false;
              }
            }else{
              this.shopList=[];
              this.shoplistShow=false;
              this.$dialog.alert({
                message:"获取门店数据失败！"
              }).then(() => {
              });
            }
          })
      },
      liClick(index){
        this.liActive=index;
      },
      timeChang(data){
        if(data.length>10){
          this.day=this.common.transForm(data.split(" - ")[0])+" - "+this.common.transForm(data.split(" - ")[1]);
          this.pathData={
            beginDate:data.split(" - ")[0],
            endDate:data.split(" - ")[1]
          }
        }else {
          this.day=this.common.transForm(data)+" - "+this.common.timeForMat(0,true);
          this.pathData={
            beginDate:data,
            endDate:this.common.timeForMat(0,false)
          }
        }
        this.shopData();
      }
    },
  }
</script>

<style scoped lang="scss">
  .tiemFixd{
    position: fixed;
    left: 0;
    top: 86px;
    width: 100%;
    z-index: 9999999999;
  }
  .shopList{
    width: 100%;
    .headers{
      width: 100%;
      height: 80px;
      padding: 0 32px;
      box-sizing: border-box;
      background:rgba(248,248,248,1);
      line-height: 80px;
      display: table;
      border-bottom: 1px solid #F0F0F0;
      border-top: 1px solid #F0F0F0;
      p{
        color:#999999;
        font-size:24px;
        font-family:PingFangSC;
        font-weight:400;
        display:table-cell;
        text-align: left;
      }
      p:nth-child(2){
        width: 178px;
        font-size:14px;
      }
      p:nth-child(3){
        width: 100px;
      }
    }
    ul{
      width: 100%;
      padding-bottom: 120px;
      li{
        padding:32px;
        line-height: 48px;
        display: table;
        box-sizing: border-box;
        border-bottom: 1px solid #F0F0F0;
        width: 100%;
        background: #fff;
        p{
          color:#999999;
          font-size:28px;
          font-family:PingFangSC;
          font-weight:400;
          display:table-cell;
          text-align: left;
        }
        p:nth-child(1){
          padding-right: 10px;
          box-sizing: border-box;
          color:rgba(102,102,102,1);
        }
        .textShow{
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
          height: inherit!important;
        }
        p:nth-child(2){
          width: 178px;
          font-weight:600;
          color:rgba(51,51,51,1);
          padding-right: 10px;
        }
        p:nth-child(3){
          width: 100px;
          font-weight:500;
          color:rgba(51,51,51,1);
        }
      }
    }
  }
  .reportOut{
    height: 372px;
    width: 100%;
    padding: 40px  40px 60px 40px;
    margin-top: 80px;
    box-sizing: border-box;
    text-align: left;
    .report{
      width: 100%;
      height: 100%;
      padding: 32px 40px;
      box-sizing: border-box;
      background:linear-gradient(215deg,rgba(255,121,60,1) 0%,rgba(255,141,89,1) 100%);
      box-shadow:0px 0px 10px 0px rgba(14,14,14,0.1);
      border-radius:8px;
      .Earnings{
        width: 100%;
        height: 40px;
        overflow: hidden;
        p{
          float: left;
          font-size:12px;
          font-family:PingFangSC;
          font-weight:400;
          color:rgba(255,255,255,1);
          line-height: 40px;
        }
        div{
          float: right;
          width:310px;
          height:40px;
          background:rgba(236,104,44,1);
          border-radius:20px;
          font-size:24px;
          font-family:PingFangSC;
          font-weight:400;
          color:rgba(255,255,255,0.7);
          line-height: 40px;
          text-align: center;
        }
      }
      .price{
        height:70px;
        font-size:50px;
        font-family:DINAlternate;
        font-weight:bold;
        color:rgba(255,255,255,1);
        line-height:70px;
        width: 100%;
      }
      .Number{
        margin-top: 30px;
        display: flex;
        div{
          flex: 1;
        }
        p:nth-child(1){
          font-size:12px;
          font-family:PingFangSC;
          font-weight:300;
          color:rgba(255,238,230,1);
          line-height: 20px;
        }
        p:nth-child(2){
          font-size:28px;
          font-family:DINAlternate;
          font-weight:bold;
          color:rgba(255,255,255,1);
          line-height: 40px;
          margin-top: 10px;
        }
      }
    }
  }
</style>



